/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';
import ServerSideRender from '@wordpress/server-side-render';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, SelectControl, RangeControl, FormTokenField, ToggleControl } from '@wordpress/components';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
export default function Edit({ attributes, setAttributes }) {

	const { productIDs, numberOfAttendees, layout, limitNumAttendees, sortAttendeeBy, sortAttendeeOrder, uniqueAttendees, enableShowName, nameFormat, enableShowGravatar, enableShowEmail, enableShowPhone, enableShowDesignation, enableShowCompany, profilePictureSize, numberOfGridItems, enableTotalAttendees } = attributes;

	const onChangeNumberOfAttendees = (val) => {
		setAttributes({ numberOfAttendees: val });
	};

	const onChangeNumberOfGridItems = (val) => {
		setAttributes({ numberOfGridItems: val });
	};

	function onChangeLayout(value) {

		setAttributes({ layout: value })

		if ('pictures' === value) {

			setAttributes({ limitNumAttendees: true });

		}

	}

	return (
		<>
			<InspectorControls>
				<PanelBody title={__('Attendee Settings', 'woocommerce-events')}>
					<FormTokenField
						label={__('Filter by Product IDs', 'woocommerce-events')}
						value={productIDs}
						onChange={(tokens) => {
							const filteredTokens = [];
							tokens.forEach(function (item, index) {
								const replacement = item.replace('#', '');
								const productID = replacement;
								if (typeof productID !== 'undefined') {
									filteredTokens.push(productID);
								}
							});
							setAttributes({ productIDs: filteredTokens });
						}}
					/>
					<SelectControl
						label={__('Sort Attendees By:', 'woocommerce-events')}
						value={sortAttendeeBy}
						options={[
							{ label: 'First Name', value: 'firstname' },
							{ label: 'Last Name', value: 'lastname' },
							{ label: 'Create Date', value: 'createdate' },
							{ label: 'Random', value: 'rand' },
						]}
						onChange={(value) =>
							setAttributes({ sortAttendeeBy: value })
						}
					/>
					<SelectControl
						label={__('Sort Attendees Order:', 'woocommerce-events')}
						value={sortAttendeeOrder}
						options={[
							{ label: 'ASC', value: 'asc' },
							{ label: 'DESC', value: 'desc' }
						]}
						onChange={(value) =>
							setAttributes({ sortAttendeeOrder: value })
						}
					/>
					<ToggleControl
						label={__('Limit number attendees', 'woocommerce-events')}
						checked={limitNumAttendees}
						onChange={() =>
							setAttributes({ limitNumAttendees: !limitNumAttendees })
						}
					/>
					{limitNumAttendees && (
						<RangeControl
							label={__('Number of attendees', 'woocommerce-events')}
							value={numberOfAttendees}
							onChange={onChangeNumberOfAttendees}
							min={2}
							max={100}
						/>
					)}
					<ToggleControl
						label={__('Display unique attendees only', 'woocommerce-events')}
						checked={uniqueAttendees}
						onChange={() =>
							setAttributes({ uniqueAttendees: !uniqueAttendees })
						}
					/>
				</PanelBody>
				<PanelBody title={__('Display Settings', 'woocommerce-events')} initialOpen={false}>
					<SelectControl
						label={__('Layout', 'woocommerce-events')}
						value={layout}
						options={[
							{ label: 'Grid', value: 'grid' },
							{ label: 'Pictures', value: 'pictures' },
							{ label: 'List', value: 'list' }
						]}
						onChange={(value) =>
							//setAttributes({ layout: value })
							onChangeLayout(value)
						}
					/>
					{'grid' == layout && (
						<RangeControl
							label={__('Number of items per row', 'woocommerce-events')}
							value={numberOfGridItems}
							onChange={onChangeNumberOfGridItems}
							min={2}
							max={8}
						/>
					)}
					{'pictures' != layout && (
					<ToggleControl
						label={__('Show name', 'woocommerce-events')}
						checked={enableShowName}
						onChange={() =>
							setAttributes({ enableShowName: !enableShowName })
						}
					/>
					)}
					{enableShowName && 'pictures' != layout && (
						<SelectControl
							label={__('Name format', 'woocommerce-events')}
							value={nameFormat}
							options={[
								{ label: 'Full Name', value: 'full_name' },
								{ label: 'First Name', value: 'first_name' },
								{ label: 'First Name + Initial', value: 'first_name_initial' },
								{ label: 'Last Name', value: 'last_name' },
								{ label: 'Initial + Last Name', value: 'initial_last_name' }
							]}
							onChange={(value) =>
								setAttributes({ nameFormat: value })
							}
						/>
					)}
					<ToggleControl
						label={__('Show Gravatar', 'woocommerce-events')}
						checked={enableShowGravatar}
						onChange={() =>
							setAttributes({ enableShowGravatar: !enableShowGravatar })
						}
					/>
					{'pictures' != layout && (
						<ToggleControl
							label={__('Show email address', 'woocommerce-events')}
							checked={enableShowEmail}
							onChange={() =>
								setAttributes({ enableShowEmail: !enableShowEmail })
							}
						/>
					)}
					{'pictures' != layout && (
						<ToggleControl
							label={__('Show phone number', 'woocommerce-events')}
							checked={enableShowPhone}
							onChange={() =>
								setAttributes({ enableShowPhone: !enableShowPhone })
							}
						/>
					)}
					{'pictures' != layout && (
						<ToggleControl
							label={__('Show company', 'woocommerce-events')}
							checked={enableShowCompany}
							onChange={() =>
								setAttributes({ enableShowCompany: !enableShowCompany })
							}
						/>
					)}
					{'pictures' != layout && (
						<ToggleControl
							label={__('Show designation', 'woocommerce-events')}
							checked={enableShowDesignation}
							onChange={() =>
								setAttributes({ enableShowDesignation: !enableShowDesignation })
							}
						/>
					)}
					<ToggleControl
						label={__('Show total attendees', 'woocommerce-events')}
						checked={enableTotalAttendees}
						onChange={() =>
							setAttributes({ enableTotalAttendees: !enableTotalAttendees })
						}
					/>
					<SelectControl
						label={__('Profile picture size', 'woocommerce-events')}
						value={profilePictureSize}
						options={[
							{ label: 'Small', value: 'small' },
							{ label: 'Medium', value: 'medium' },
							{ label: 'Large', value: 'large' },
						]}
						onChange={(value) =>
							setAttributes({ profilePictureSize: value })
						}
					/>
				</PanelBody>
			</InspectorControls>
			<div {...useBlockProps()}>
				<ServerSideRender
					block="woocommerce-events/fooevents-event-attendees"
					attributes={attributes}
				/>
			</div>
		</>
	);
}
